// frontend/src/components/Navbar.js
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';

const Navbar = () => {
  const [user, setUser] = useState(null);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const location = useLocation();
  const navigate = useNavigate();

  //   
  useEffect(() => {
    const updateUser = () => {
      const token = localStorage.getItem('token');
      const userRole = localStorage.getItem('role');
      if (token && userRole) {
        setUser({ role: userRole });
      } else {
        setUser(null);
      }
    };
    updateUser();
    window.addEventListener('storage', updateUser);
    window.addEventListener('authChange', updateUser);
    return () => {
      window.removeEventListener('storage', updateUser);
      window.removeEventListener('authChange', updateUser);
    };
  }, []);

  // ===   ===
  const handleLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('role');
    localStorage.removeItem('userId');
    setUser(null);
    setIsMobileMenuOpen(false);
    window.dispatchEvent(new Event('authChange'));
    navigate('/login', { replace: true });
  };

  const handleLinkClick = () => setIsMobileMenuOpen(false);
  const toggleMobileMenu = () => setIsMobileMenuOpen(prev => !prev);

  //  
  const navItemVariants = {
    hover: { scale: 1.03, transition: { duration: 0.2 } },
    tap: { scale: 0.98, transition: { duration: 0.1 } }
  };

  return (
    <motion.nav
      className="bg-[#18191a] text-white p-4 border-b border-[#3a3b3c] sticky top-0 z-50 shadow-sm"
      initial={{ opacity: 0, y: -10 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.4 }}
    >
      <div className="max-w-7xl mx-auto flex justify-between items-center">
        {/* ===  +  === */}
        <Link to="/" onClick={handleLinkClick} className="flex items-center gap-3">
          <motion.div whileHover={{ scale: 1.08 }} whileTap={{ scale: 0.95 }}>
            <img
              src="/icon.png"
              alt="SHOSE NET"
              className="w-14 h-14 sm:w-16 sm:h-16 object-contain"
            />
          </motion.div>
          <span className="text-xl sm:text-2xl font-bold text-white">
            Web Shose
          </span>
        </Link>

        {/*   */}
        <button
          className="md:hidden p-2 rounded-lg hover:bg-[#242526] transition"
          onClick={toggleMobileMenu}
          aria-label=""
        >
          <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth="2"
              d={isMobileMenuOpen ? "M6 18L18 6M6 6l12 12" : "M4 6h16M4 12h16M4 18h16"}
            />
          </svg>
        </button>

        {/*     */}
        <div className="hidden md:flex items-center gap-4">
          {renderNavLinks(handleLinkClick)}
          {renderAuthButton(handleLogout)}
        </div>
      </div>

      {/*    -     */}
      <AnimatePresence>
        {isMobileMenuOpen && (
          <motion.div
            className="md:hidden mt-4 flex flex-col gap-2 overflow-hidden"
            initial={{ opacity: 0, y: -10 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -10 }}
            transition={{ 
              duration: 0.18, 
              ease: "easeOut",
              staggerChildren: 0.04
            }}
            style={{ willChange: 'transform, opacity' }}
          >
            {renderNavLinks(handleLinkClick)}
            {renderAuthButton(handleLogout, true)}
          </motion.div>
        )}
      </AnimatePresence>
    </motion.nav>
  );

  // ===  ===
  function renderNavLinks(onClick) {
    const links = [
      { to: "/", label: "" },
      { to: "/vendors", label: "" },
      ...(user ? [{ to: "/orders", label: "" }] : []),
      ...(user?.role === 'vendor' ? [{ to: "/vendor", label: " " }] : []),
      ...(user?.role === 'admin' ? [
        { to: "/admin", label: " " },
        { to: "/create-vendor", label: " ", color: "green" },
        { to: "/create-customer", label: " ", color: "green" },
        { to: "/block-customer", label: " ", color: "red" },
        { to: "/admin/pending-customers", label: " ", color: "yellow" }
      ] : [])
    ];

    return links.map((link, index) => (
      <motion.div
        key={link.to}
        initial={{ opacity: 0, x: -20 }}
        animate={{ opacity: 1, x: 0 }}
        transition={{ delay: index * 0.04 }}
      >
        <NavLink 
          to={link.to} 
          currentPath={location.pathname} 
          onClick={onClick} 
          color={link.color}
        >
          {link.label}
        </NavLink>
      </motion.div>
    ));
  }

  // ===   / ===
  function renderAuthButton(onLogout, isMobile = false) {
    if (user) {
      return (
        <motion.button
          onClick={onLogout}
          className={`px-4 py-2 bg-red-600/80 rounded-lg text-sm font-medium hover:bg-red-700 transition ${isMobile ? 'w-full text-right' : ''}`}
          variants={navItemVariants}
          whileHover="hover"
          whileTap="tap"
        >
           
        </motion.button>
      );
    }
    return (
      <motion.div
        className={`px-4 py-2 bg-green-600/80 rounded-lg text-sm font-medium hover:bg-green-700 transition ${isMobile ? 'w-full text-right' : ''}`}
        variants={navItemVariants}
        whileHover="hover"
        whileTap="tap"
      >
        <Link to="/login" onClick={handleLinkClick}> </Link>
      </motion.div>
    );
  }
};

// ===   ===
const NavLink = ({ to, currentPath, children, onClick, color = 'blue' }) => {
  const isActive = currentPath === to;
  const activeBg =
    color === 'green' ? 'bg-green-600/30' :
    color === 'red' ? 'bg-red-600/30' :
    color === 'yellow' ? 'bg-yellow-600/30' :
    'bg-blue-600/30';
  const hoverBg =
    color === 'green' ? 'hover:bg-green-600/20' :
    color === 'red' ? 'hover:bg-red-600/20' :
    color === 'yellow' ? 'hover:bg-yellow-600/20' :
    'hover:bg-blue-600/20';

  return (
    <Link
      to={to}
      onClick={onClick}
      className={`block px-4 py-2 rounded-lg text-sm font-medium transition ${isActive ? activeBg : hoverBg} ${isActive ? 'ring-2 ring-white/50' : ''}`}
    >
      {children}
    </Link>
  );
};

export default Navbar;
